import React, {useEffect} from "react";
import {Box, Flex} from "../Grid";
import styled, {system} from "../../style";
import Input from "../Input";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faArrowLeft} from "@fortawesome/free-solid-svg-icons";

const Background = styled(Box)`
  ${system({backdropFilter: true})}
`;

const InputWrapper = styled(Flex)`
  ${system({backdropFilter: true})}
`;

const MobileSearchBar = props => {
  const {placeholder, enable, handleOnClickBack, inputRef, ...rest} = props;

  // const inputRef = useRef();
  //
  useEffect(() => {
    if (inputRef && inputRef.current) {
      inputRef.current.focus();
    }
  });

  return (
    <Box display={[enable ? "block" : "none", enable ? "block" : "none", "none", "none"]}>
      <Background
        top={0}
        left={0}
        width="100%"
        height="100vh"
        bg="mobileInstantSearchBg"
        backdropFilter="blur(10px)"
        zIndex={1}
        position="fixed"
      />

      <InputWrapper
        top={0}
        left={0}
        width="100%"
        zIndex={3}
        position="fixed"
        alignItems="center"
        justifyContent="flex-end"
        bg="mobileInstantSearchBg"
        backdropFilter="blur(10px)"
      >
        <Flex height={0} alignContent="center" flexWrap="wrap" cursor="pointer" ml={3} onClick={handleOnClickBack}>
          <FontAwesomeIcon icon={faArrowLeft}/>
        </Flex>
        <Flex display="block" flexGrow="1" position="relative" ml={3} mr={2} my={2}>
          <Box as="form">
            <Input
              ref={inputRef}
              type="search"
              autoComplete="off"
              placeholder={placeholder}
              bg="grey.0"
              px={3}
              py={2}
              width="100%"
              height={0}
              {...rest}
            />
          </Box>
        </Flex>
      </InputWrapper>
    </Box>
  );
};

MobileSearchBar.defaultProps = {
  placeholder: "Search"
};

export default MobileSearchBar;
