import axios from "axios";

import config from "../config";

const API_ENDPOINT = config.apiEnpoint;

const suggest = (bodyData) => {
  const url = `${API_ENDPOINT}/guest/shopify/suggest`;
  const result = axios({
    url,
    method: "post",
    headers: {
      "Content-Type" : "application/json",
    },
    data: bodyData,
  });
  return result;
};

const suggestAPI= {
  suggest
};

export default suggestAPI;

