const getInputs = () => {
  const result = [];
  document.querySelectorAll('form[action="/search"]').forEach(el => {
    result.push(el.querySelector('input[name="q"]'));
  });
  return result;
};

const getInstantSearchPosition = (input, widgetWidth) => {
  // Get input position and size
  const inputRect = input.getBoundingClientRect();

  // Calculate top
  const top = inputRect.top + inputRect.height;

  // Calculate left
  let left = 0;
  if (window.innerWidth - inputRect.left < widgetWidth) {
    left = inputRect.left - (widgetWidth - inputRect.width);
  } else {
    left = inputRect.left;
  }

  // minus 8px to help customers recoginze that there are more results,
  // want to see more, scroll down
  const maxHeight = window.innerHeight - top - 8;

  return { top, left, maxHeight };
};

// eslint-disable-next-line no-template-curly-in-string
const formatMoney = (cents, format = '${{amount}}') => {
  if (typeof cents === "string") {
    cents = cents.replace(".", "");
  }
  let value = "";
  const placeholderRegex = /\{\{\s*(\w+)\s*\}\}/;
  const formatString = format;

  function formatWithDelimiters(number, precision, thousands, decimal) {
    thousands = thousands || ",";
    decimal = decimal || ".";

    if (isNaN(number) || number === null) {
      return 0;
    }

    number = (number / 100.0).toFixed(precision);

    const parts = number.split(".");
    const dollarsAmount = parts[0].replace(
      /(\d)(?=(\d\d\d)+(?!\d))/g,
      "$1" + thousands
    );
    const centsAmount = parts[1] ? decimal + parts[1] : "";

    return dollarsAmount + centsAmount;
  }

  switch (formatString.match(placeholderRegex)[1]) {
    case "amount":
      value = formatWithDelimiters(cents, 2);
      break;
    case "amount_no_decimals":
      value = formatWithDelimiters(cents, 0);
      break;
    case "amount_with_comma_separator":
      value = formatWithDelimiters(cents, 2, ".", ",");
      break;
    case "amount_no_decimals_with_comma_separator":
      value = formatWithDelimiters(cents, 0, ".", ",");
      break;
    case "amount_no_decimals_with_space_separator":
      value = formatWithDelimiters(cents, 0, " ");
      break;
    case "amount_with_apostrophe_separator":
      value = formatWithDelimiters(cents, 2, "'");
      break;
    default:
      value = formatWithDelimiters(cents, 2);
      break;
  }

  return formatString.replace(placeholderRegex, value);
};

export { getInputs, getInstantSearchPosition, formatMoney };
