import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import * as serviceWorker from './serviceWorker';
import settingAPI from "./services/setting";
import config from "./config";
import axios from "axios";

// Get access token
const scripts = document.getElementsByTagName('script');
for (let script of scripts) {
  if (script.src && script.src.indexOf(config.scriptIdentifier) > -1) {
    const url = new URL(script.src);
    const params = new URLSearchParams(url.search);
    axios.defaults.headers.common['authorization'] = "Bearer " + params.get("tk");
    break;
  }
}

// Request to get setting data
settingAPI.getSettings().then(response => {
  const {data} = response;

  const widgetContainer = document.createElement("div");
  widgetContainer.setAttribute("id", "flashsearch-instant-search-container");

  document.body.appendChild(widgetContainer);

  ReactDOM.render(<App settings={data}/>, widgetContainer);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
  serviceWorker.unregister();

}).catch(error => console.log(`Can't get settings: ${error}`));


