import React, { forwardRef } from "react";

import { Box } from "../Grid";
import styled, { typography, system } from "../../style";

const StyledText = styled(Box)`
  ${typography}
  ${system({wordWrap: true})}
  ${system({textDecoration: true})}
`;

const Text = forwardRef((props, ref) =>
  <StyledText
    ref={ref}
    fontSize="body"
    fontWeight="normal"
    color="bodyText"
    wordWrap="break-word"
    { ...props }
  />
);

export default Text;
