const buildMerchandisingRules = (rules) => {
  return rules;
};

const buildDefaultSearchFields = (searchFields) => {
  return searchFields.map(field => {
    return {name: field.name, weight: field.weight}
  });
};

const buildSearchBodyData = (settings, q) => {
  const {
    pinnedProducts, merchandisingRules, synonyms,
    defaultSearchFields, metafieldSearchFields,
    variantMetafieldSearchFields
  } = settings;

  const bodyData = {"q": q};

  // Pinned products
  if (settings && pinnedProducts) {
    if (q in pinnedProducts.applyToAll) {
      bodyData["pinnedProducts"] = pinnedProducts.applyToAll[q];
    } else if (q in pinnedProducts.applyToInstantSearch) {
      bodyData["pinnedProducts"] = pinnedProducts.applyToInstantSearch[q];
    }
  }

  // Merchandising
  let merchandisingRulesResult = [];

  if (settings && merchandisingRules) {
    // Apply to all (instant search, search results)
    // If apply to all queries
    if (merchandisingRules.applyToAll) {
      if (merchandisingRules.applyToAll.all && merchandisingRules.applyToAll.all.length > 0) {
        merchandisingRulesResult = [...merchandisingRulesResult, ...buildMerchandisingRules(merchandisingRules.applyToAll.all)];
      }

      // If apply to specific query
      if (q in merchandisingRules.applyToAll.specific) {
        merchandisingRulesResult = [...merchandisingRulesResult, ...buildMerchandisingRules(merchandisingRules.applyToAll.specific[q])];
      }
    }

    // Apply to instant search
    if (merchandisingRules.applyToInstantSearch) {
      // If apply to all queries
      if (merchandisingRules.applyToInstantSearch.all && merchandisingRules.applyToInstantSearch.all.length > 0) {
        merchandisingRulesResult = [...merchandisingRulesResult, ...buildMerchandisingRules(merchandisingRules.applyToInstantSearch.all)];
      }

      // If apply to specific query
      if (q in merchandisingRules.applyToInstantSearch.specific) {
        merchandisingRulesResult = [...merchandisingRulesResult, ...buildMerchandisingRules(merchandisingRules.applyToInstantSearch.specific[q])]
      }
    }
  }

  if (merchandisingRulesResult.length > 0) {
    bodyData["merchandisingRules"] = merchandisingRulesResult;
  }

  // Synonyms
  if (synonyms && synonyms.analyzer !== "") {
    bodyData["synonymsAnalyzer"] = synonyms.analyzer;
  }

  // Default search fields
  if (defaultSearchFields) {
    bodyData["defaultSearchFields"] = buildDefaultSearchFields(defaultSearchFields);
  }

  // Metafield search field
  if (metafieldSearchFields) {
    bodyData["metafieldSearchFields"] = metafieldSearchFields;
  }

  // Variant metafield search field
  if (variantMetafieldSearchFields) {
    bodyData["variantMetafieldSearchFields"] = variantMetafieldSearchFields;
  }

  return bodyData;
};

export default buildSearchBodyData;
