import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Link from "../Link";
import Item from "./Item";
import { Box } from "../Grid";
import Text from "../Text";
import { formatMoney } from "../../utils";

const Detail = styled.div``;

const Product = props => {
  const { title, featuredImage, priceMin, compareAtPriceMin } = props;

  // Get currency, money format from Shopify object
  const [currency, setCurrency] = useState("USD");
  const [moneyFormat, setMoneyFormat] = useState("");
  useEffect(() => {
    // eslint-disable-next-line no-unused-expressions
    // eslint-disable-next-line no-undef
    setCurrency(Shopify.currency.active);
    try {
      // eslint-disable-next-line no-undef
      setMoneyFormat(theme.moneyFormat);
    } catch (e) {}
  }, []);

  // Format price with currency
  const [formattedPrice, setFormattedPrice] = useState(0.00);
  const [formattedCpaPrice, setFormattedCpaPrice] = useState(0.00);
  useEffect(() => {
    if (moneyFormat) {
      setFormattedPrice(formatMoney(parseFloat(priceMin).toFixed(2), moneyFormat));
      setFormattedCpaPrice(formatMoney(parseFloat(compareAtPriceMin).toFixed(2), moneyFormat));
    } else {
      const priceFormatter = new Intl.NumberFormat(undefined, {
        style: "currency",
        currency: currency
      });
      setFormattedPrice(priceFormatter.format(priceMin));
      setFormattedCpaPrice(priceFormatter.format(compareAtPriceMin));
    }
  }, [compareAtPriceMin, currency, moneyFormat, priceMin]);

  const PriceMarkup = priceMin ? <Text as="span" fontWeight="bold">{formattedPrice}</Text> : null;
  const compareAtPriceMarkup = compareAtPriceMin ? (
    <Text as="span" textDecoration="line-through" color="grey.1" mr={1}>{formattedCpaPrice}</Text>
  ) : null;

  return (
    <Item url={props.url}>
      <Link
        href={props.url}
        title={title}
        minWidth={1}
        minHeight={1}
        marginRight={3}
      >
        <Box
          as="img"
          maxWidth={1}
          maxHeight={1}
          src={featuredImage.originalSrc}
          alt=""
        />
      </Link>
      <Detail>
        <Text mb={1}>{title}</Text>
        <Box>
          {compareAtPriceMarkup}
          {PriceMarkup}
        </Box>
      </Detail>
    </Item>
  );
};

export default Product;
