import {suggest} from "../appRedux/slices/Suggest";
import {useDispatch} from "react-redux";
import {useCallback} from "react";
import buildSearchBodyData from "./searchBodyDataBuilder";

const useHandleInput = (settings) => {
  const dispatch = useDispatch();


  const handleOnInput = useCallback(e => {
    const {usageStatus} = settings;
    // Check whether instant search feature disabled or enabled
    const {allFeatures, instantSearch} = usageStatus;
    if (!allFeatures || !instantSearch) {
      return;
    }

    // Set query
    const q = e.target.value.trim();
    if (!q) {
      return
    }

    const bodyData = buildSearchBodyData(settings, q);

    dispatch(suggest(bodyData));

  }, [dispatch, settings]);

  const handleOnKeyDown = useCallback((e) => {
    const {usageStatus, redirect} = settings;
    // Check whether instant search feature disabled or enabled
    const {allFeatures, instantSearch} = usageStatus;
    if (!allFeatures || !instantSearch) {
      return;
    }

    // Redirect
    if (e.key === "Enter") {
      e.preventDefault();

      const q = e.target.value.trim();

      // If in redirect
      if (q in redirect) {
        window.location = redirect[q];

        // If not, redirect to normal search page
      } else {
        window.location = "/search?q=" + q
      }
    }
  }, [settings]);

  return {handleOnInput, handleOnKeyDown}
};

export default useHandleInput;
