import {call, put, takeLatest, fork, all} from "redux-saga/effects";

import {
  fetchError,
  fetchStart,
  fetchSuccess,
} from "../slices/Common";
import {suggest, suggestSuccess} from "../slices/Suggest";
import suggestAPI from "../../services/suggest";

function* suggestRequest({payload}) {
  // let params;
  // if (metadata) {
  //   params = metadata;
  // }

  try {
    yield put(fetchStart());

    const {data} = yield call(suggestAPI.suggest, payload);

    // data = result.data;
    // if (params) {
    //   const result = yield call(suggestAPI.suggest, query, params);
    //   data = result.data;
    // } else {
    //   const result = yield call(suggestAPI.suggest, query);
    //   data = result.data;
    // }

    yield put(suggestSuccess(data));
    yield put(fetchSuccess());
  } catch (error) {
    yield put(fetchError())
  }
}

function* suggestSaga() {
  yield takeLatest(suggest, suggestRequest)
}

export default function* rootSaga() {
  yield all([
    fork(suggestSaga),
  ]);
}
