import React from "react";
import Products from "./Products";
import Suggestions from "./Suggestions";
import {Box, BoxShadow} from "../Grid";
import Text from "../Text";
import ViewAllResult from "./ViewAllResults";
import styled from "../../style";
import Collections from "./Collections";
import Pages from "./Pages";

const StyledWrapper = styled(BoxShadow)`
  overscroll-behavior-y: contain;
`;

const InstantSearch = props => {
  const {
    suggestions, collections, pages, products, didYouMean, productPerPage,
    query, enable,
    // position
    top, left, maxHeight,
    input,
  } = props;

  const DidYouMeanWrapper = styled(Box)`
cursor: pointer;
`;

  const handleClickDidYouMean = (e) => {
    e.stopPropagation();
    e.preventDefault();
    const event = new Event('input', {
      'bubbles': true,
      'cancelable': true
    });
    input.value = didYouMean;
    input.dispatchEvent(event);
  };

  const suggestionMarkup = suggestions && suggestions.length > 0 ? (
    <Suggestions suggestions={suggestions} query={query}/>
  ) : null;

  const collectionMarkup = collections && collections.length > 0 ? (
    <Collections collections={collections} query={query}/>
  ) : null;

  const pageMarkup = pages && pages.length > 0 ? (
    <Pages pages={pages} query={query}/>
  ) : null;

  // did you mean markup
  const didYouMeanMarkup = didYouMean ? (
    <Text textAlign="center" py={2}>
      Sorry, nothing found for <Text as="span" fontWeight="bold">{query}</Text>.&nbsp;
      Did you mean: <DidYouMeanWrapper
      as="span" cursor="pointer" bg="grey.0" px={2} py={1} borderRadius={4}
      onClick={handleClickDidYouMean}
      onMouseDown={(e) => e.preventDefault()}
    >
      <Text
        as="span">{didYouMean}</Text></DidYouMeanWrapper> ?
    </Text>
  ) : null;

  const productMarkup = products && products.length > 0 ? (
    <Products products={products}/>
  ) : null;

  const notFoundMarkup = enable && suggestionMarkup === null && collectionMarkup === null && pageMarkup === null && didYouMeanMarkup === null && productMarkup === null ? (
      <Text>
        Sorry, nothing found for <Text as="span" fontWeight="bold">"{query}"</Text>
      </Text>
  ) : null;

  // View all results markup
  const viewAllResultMarkup = ((products.length === productPerPage) && query) ? (
    <ViewAllResult query={query}/>
  ) : null;

  return (
    <StyledWrapper
      bg="background"
      position="fixed"
      overflow="auto"
      boxShadow="instantSearch"

      border={["none", "none", 1, 1]}
      borderRadius={[0, 0, 1, 1]}
      zIndex={2}
      display={enable ? "block" : "none"}
      top={['6px', '6px', `${top}px`, `${top}px`]}
      left={[0, 0, `${left}px`, `${left}px`]}
      pt={["mobileInstantSearchPt", "mobileInstantSearchPt", 0, 0]}
      width={["100%", "100%", 2, 2]}
      maxHeight={["calc(100% - 6px)", "calc(100% - 6px)", `${maxHeight}px`, `${maxHeight}px`]}
      onTouchMove={() => document.activeElement.blur()}
    >
      <Box m={[3, 3, 4, 4]} mb={[3, 3]} mt={[2, 2]}>
        {suggestionMarkup}
        {collectionMarkup}
        {pageMarkup}
        {didYouMeanMarkup}
        {productMarkup}
        {notFoundMarkup}
        {viewAllResultMarkup}
      </Box>
    </StyledWrapper>
  );
};

export default InstantSearch;
