import {createSlice} from "@reduxjs/toolkit";

const INIT_STATE = {
  collections: [],
  didYouMean: "",
  pages: [],
  productPerPage: 0,
  products: [],
  query: "",
  suggestions: [],
};

const slice = createSlice({
  name: "suggest",
  initialState: INIT_STATE,
  reducers: {
    suggest(state) { return state},

    suggestSuccess(state, action) {
      return {...state, ...action.payload}
    },
  }
});

export const {suggest, suggestSuccess} = slice.actions;

export default slice.reducer;
