import React, { useContext } from "react";
import styled, { ThemeContext } from "../../style";
import Highlighter from "react-highlight-words";
import Text from "../Text";

const Highlight = ({ children, highlightIndex }) => (
  <span>{children}</span>
);

const StyledHighlightText = styled(Text)`
  & .flashsearch-unhighlight-text {
    font-weight: ${props => props.unhighlightFontWeight}
  }
`;

const HighlightText = props => {
  const { searchWord, textToHighlight, className } = props;
  const themeContext = useContext(ThemeContext);
  return (
    <StyledHighlightText className={className} unhighlightFontWeight={themeContext.fontWeights.bold}>
      <Highlighter
        searchWords={[searchWord]}
        autoEscape={true}
        textToHighlight={textToHighlight}
        highlightStyle={{color: "inherit"}}
        unhighlightClassName="flashsearch-unhighlight-text"
        highlightTag={Highlight}
      />
    </StyledHighlightText>
  );
}

export default HighlightText;
